.header{
    width: 100%;
    position: fixed;
    top:0;
}
.is-sticky {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    box-shadow: 0 2px 24px 0 rgb(0 0 0 / 15%);
    background-color: #ffffff !important;
    animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
    padding-top: 0px;
    padding-bottom: 0px;
}
.box{
    height: 100px;
    display: flex;
    justify-content: space-around;
    /*border-bottom: 1px solid gray;*/
    font-weight: 700;
    font-family: monospace;
    color: white;
}

.menu{
    display: flex;
    align-items: center;
    font-size: 22px;
    line-height: 28px;
    gap:30px;
    margin-right: 30px;
}
.menu li:after {
    content: "";
    display: block;
    height: 3px;
    width: 0;
    background: transparent;
    transition: width 0.7s ease, background-color 0.5s ease;
}

.menu li:hover:after {
    width: 100%;
    background: #ffdd40;
}
.mobile_btn{
    display: none;
}
.logo_image_menu{
    display: none;
}



@media (max-width: 880px) {
    .mobile_btn{
        display: block;
        position: absolute;
        right: 10px;
        top: 27px;
        cursor: pointer;
        z-index: 10;
    }
    /*.cal_me_btn button{*/
    /*    display: none;*/
    /*}*/



    .menu{
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: fixed;
        left: -100%;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        height: 100vh;
        background: #020130;

        z-index: 10;
        transition: left 1s;
    }
    .logo_image_menu{
        display: block;

    }
    .logo_image_menu img{
        margin-top: -100px;
        height: 300px;

    }
    .active{
        left: 0;
    }
    .menu a{
        font-size: 28px;
    }

    .cal_me_btn_burger button{
        display: block;
        font-size: 20px;
        padding: 10px 60px;
        border-radius: 40px;
        margin: 25px 0px;
        cursor: pointer;
    }
    .cal_me_btn button{
        padding: 5px 5px;

        margin: auto;
        margin-right: 50px;
    }
    .cal_me_btn button span{
        display: none;
    }
}